@use "base/variables" as v;
@use "base/mixins" as m;

.sidebar{
    
    background-color: v.$indigo;
    height: 0;
    overflow: hidden;   
    opacity: 0;
    transition-property: opacity;
    transition-duration: 500ms;

    &.mostrar{
        opacity: 1;
        padding: 2rem;
        height: 100%;
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        background-color: rgb(0 0 0 / .95);
        @include m.tablet{
            display: none;
                 
            }

    }

    @include m.tablet{
        display: block;
        opacity: 1;
        padding: 2rem;
        height: 100%;
        background-color: v.$indigo;         
        }

        .contenedor-sidebar{
            display: flex;
            justify-content: space-between;
            align-items: center;

            h2{
                // color: v.$blanco;
                @include m.textoDegradado(v.$cyan);
                margin: 0;
                text-align: center;
                font-size: 5rem !important;
            }

            .cerrar-menu{
                display: flex;
                justify-content: center;
                height: 2rem;
                width: 2rem;

                img{
                &:hover{
                    cursor: pointer;
                }

                @include m.tablet{
                display: none;
                }
            }
            }
        }
    

    .sidebar-nav{
        margin-top: 5rem;

        a{
            color: v.$blanco;
            display: block;
            margin-bottom: 2.5rem;
            padding: 0.7rem;
            transition-property: background-color;
            transition-duration: 300ms;
            font-size: 2.4rem;
            text-align: center;

            &:hover,
            &.activo{
                background-color: darken(v.$indigo, 20%);
                border-radius: 3rem;
            }

            @include m.tablet {
                font-size: 1.6rem;
                margin-bottom: 1.5rem;
                text-align: left;
            }
        }
    }

    .cerrar-sesion-mobile{
        margin-top: 10rem;

        .cerrar-session{
            @include m.boton(v.$rojo, v.$blanco);
        }

        @include m.tablet{
            display: none;
        }
    }
}