@use "base/variables" as v;
@use "base/mixins" as m;

.crear{
    margin-top: 5rem;

    .uptask{
        
        @include m.textoDegradado(v.$amarillo);
       
    }
    .fondo-titulo{
        @include m.fondoDegradadoOscuro(v.$rojo);
       
     }

    input[type="submit"]{
        @include m.boton(v.$amarillo, v.$blanco)
    }

    @include m.acciones(v.$amarillo);
}