@use "base/variables" as v;
@use "base/mixins" as m;

.modal{
   
    position: absolute;
    background-color: rgb(0 0 0 / 0.8);
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;

    .formulario{
        max-width: 60rem;
        background-color: lighten(v.$cyan, 15%);        
        padding: 2rem;
        margin: 0 auto;        
        display: block;
        transform: translateY(-25rem);
        transition-property: all;
        transition-duration: 700ms;
        transition-timing-function: ease-in-out;
        opacity: 0;
        border-radius: 1rem;

        legend{
            font-size: 2rem;
            text-align: center;
            font-weight: bold;
            margin-bottom: 2rem;
            display: block;
            
        }

        input[type="text"]{
            
            border-radius: 0.5rem;
        }

        .opciones{
            @include m.tablet{
            display: flex;
            justify-content: space-between;
            }                
        }

        .submit-nueva-tarea{
            @include m.boton(v.$indigo, v.$blanco);
            
        }

        .cerrar-modal{
            @include m.boton(v.$naranja, v.$blanco);
          
        }

        label{
                font-size: 2rem;
                font-weight: bold;
                
            }

         &.animar{
            position: relative;
            top: 50%;
            transform: translateY(-50%);
            opacity: 1;
         }

         &.cerrar{
            
            transform: translateY(-200%);
            opacity: 0;
         }
    }
}