@use "base/variables" as v;
@use "base/mixins" as m;

.no-proyectos{
    a{
        color: v.$indigo;

        &:hover{
            color: darken(v.$indigo , 20%);
        }
    }
}
.listado-proyectos{
    
    list-style: none;
    padding: 0;
    margin-top: 3rem;
    @include m.grid(1, 2rem);
    
    @include m.tablet{
    @include m.grid(2, 2rem);
    }

    @include m.desktop{
    @include m.grid(3, 2rem);
    }
    .proyecto{
        
        @include m.fondoDegradadoClaro(v.$cyan);
        min-height: 10rem;
        display: flex;
        align-items: center;
        padding: 2rem;
        line-height: 2;
        border-radius: 1rem;

        &:hover{ 
            @include m.fondoDegradadoOscuro(v.$morado);
        } 
    }

    a, p{
        @include m.textoDegradado(v.$naranja);
        font-size: 2.5rem !important;
        font-weight: bold;
        padding: 1rem;

        &:hover{
            color: v.$blanco;
            cursor: pointer;
            
        }
    }
}