@use "base/variables" as v;
@use "base/mixins" as m;


.dashboard{
    background-color: v.$grisClaro;

    @include m.tablet{
        display: flex;
        height: 100vh; // VH viewport 
        
    }

    .principal{
        flex: 1;
        overflow: scroll;
        
        h2{
            @include m.textoDegradado(v.$cyan);
            text-align: center;
            font-size: 3.5rem !important;
           
        }
    }

    .contenido{
        padding: 2rem;
    }

   .enlace{
    color: darken(v.$naranja, 10%);
         
     &:hover{
         color: lighten(v.$naranja, 20%);
     }
   }
}