@use "base/variables" as v;
@use "base/mixins" as m;

.olvide{
    margin-top: 5rem;

    .uptask{
        
        @include m.textoDegradado(v.$indigo);
       
    }
    .fondo-titulo{
        @include m.fondoDegradadoOscuro(v.$rosa);
       
     }

    input[type="submit"]{
        @include m.boton(v.$indigo, v.$blanco)
    }

    @include m.acciones(v.$indigo);
}