@use 'variables' as v;

*, *:before, *:after {
    box-sizing: inherit;
}
html {
    font-size: 62.5%;
    box-sizing: border-box;
    height: 100%;
}
body {
    font-family: v.$fuente_textos;
    font-size: 1.6rem;
    min-height: 100%;
}
.contenedor {
    width: 95%;
    max-width: 1200px;
    margin: 0 auto;
}
.contenedor-sm {
    max-width: 70rem;
    margin: 0 auto;
}
a {
    text-decoration: none;
}
img {
    max-width: 100%;
}
.contenedor_pagina-error{
    background: linear-gradient(to right, v.$rosa 0%, lighten(v.$rosa, 30%) 100%) ;
.pagina-error{
    background: linear-gradient(to right, v.$cyan 0%, lighten(v.$cyan, 30%) 100%) ;        
        color: transparent;
        background-clip: text;
        text-align: center;
    }
}