@use "base/variables" as v;
@use "base/mixins" as m;

.barra-mobile{
    background-color: v.$indigo;
    padding: 2rem 4rem;
    display: flex;
    justify-content: space-between;
    align-items: center;

    h1{
        @include m.textoDegradado(v.$cyan);
        margin: 0;
        text-align: center;
        font-size: 5rem !important;
    }

    .menu{
        img{
            width: 0.8rem;

            &:hover{
                cursor: pointer;
            }
        }
    }

    @include m.tablet{
        display: none;
    }
}

.barra{
    display: none;

    @include m.tablet {
        display: flex;
        justify-content: space-between;
        align-items: center;
        background-color: v.$blanco;
    padding: 1rem 2rem;
    box-shadow: 1px 12px 9px -13px rgba(0, 0, 0, 0.6);
    }

    p{
        margin: 0;
         color: v.$indigo;
        font-size: 1.5rem;
        font-weight: 500;
    }

    span{
        font-weight: 700;
        @include m.textoDegradado(v.$rosa);
        font-size: 1.5rem !important;
    }

    .cerrar-session{
        @include m.boton( darken(v.$indigo, 20%), v.$blanco);
        font-size: 1.2rem;
        padding: 1rem;
        border-radius: 3rem;
    }
}