@use "base/variables" as v;
@use "base/mixins" as m;

.alerta{
    padding: 1rem 1rem 1rem 2rem;
    text-transform: uppercase;
    font-weight: 900;
    margin: 1rem 0;
    font-size: 1.4rem;
    max-width: 80rem;
    

&.error{
    background-color: lighten(v.$rojo, 60%);
    color: v.$rojo;
    border-left: 0.5rem solid v.$rojo;
}

&.correcto{
    background-color: lighten(v.$verde, 60%);
    color: v.$verde;
    border-left: 0.5rem solid v.$verde;
}
&:last-of-type{
    margin-bottom: 0;
}
}