@use "base/variables" as v;
@use "base/mixins" as m;


.filtros{
background-color: v.$blanco;
margin-top: 2rem;
border-radius: 1rem;
padding: 2rem;

    h3{
       
        background: linear-gradient(to right, v.$cyan 0%, darken(v.$cyan, 20%) 100%) ;        
        color: transparent;
        background-clip: text;    
       font-size: 2.8rem;
       @include m.tablet{
        margin: 0;
    }
    }   
    .filtros-inputs{

       @include m.tablet{
           display: flex;
           align-items: center;
           gap: 3rem;
           
       }
    }

    

   .campo{
        display: grid;
        grid-gap: 1rem;        
        margin-bottom: 2rem;
    
        #todas{
            grid-column: 2;
        }

    @include m.tablet{        
        display: flex;
        justify-content: space-between;
        align-items: center;
        gap: 2rem;
        margin-bottom: 0;
    }

    input[type="radio"]{
        accent-color: darken(v.$cyan, 20%);
        width: 1.6rem;
        height: 1.6rem;

        &:hover{
            cursor: pointer;
        }
    }
       
   }
}