@use "base/variables" as v;
@use "base/mixins" as m;

.formulario{
    margin-top: 5rem;

    .perfil{
        flex-direction: column;
        align-items: flex-start !important;
        
        input{
            margin-bottom: 2rem;

        }

    }
    .campo{
        display: flex;
        align-items: center;
        margin-bottom: 2rem;
    }
    label{
        flex: 0 0 10rem;
    }
    input{
        flex: 1;
        padding: 1rem;
        border: none;
        background-color: lighten(v.$gris, 40%)
        }

        input:not([type="submit"]){
            background-color: v.$blanco;
 
        }
 
        input[type="submit"]{
 
         @include m.boton(v.$indigo, v.$blanco);
         padding: 1rem;
         text-transform: uppercase;
         font-weight: bold;
         font-size: 1.4rem;

         &.input-perfil{
            @include m.boton(v.$amarillo, v.$blanco);
            padding: 1rem;
         text-transform: uppercase;
         font-weight: bold;
         font-size: 1.4rem;
         }
        }

       
}